import { createRouter, createWebHistory } from "vue-router";
import HomePage from "@/views/HomePage";

const routes = [
  {
    path: "/",
    name: "Home",
    component: HomePage,
  },
  {
    path: "/client",
    name: "Client",
    component: () => import("@/views/Client"),
  },
];
const router = createRouter({
  history: createWebHistory(),
  routes,
});
export default router;
